.App {
  font-family: 'Arial', sans-serif;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Navbar styles */
.navbar {
  background-color: #333;
  color: white;
  padding: 1rem 2rem;
}

.navbar ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar li {
  margin-right: 1rem;
}

.navbar a {
  color: white;
  text-decoration: none;
}

/* Form styles */
.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
}

.form-group input {
  width: 100%;
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.btn {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.btn-primary {
  background-color: #007bff;
  color: white;
}

.btn-success {
  background-color: #28a745;
  color: white;
}

/* Voting page styles */
.candidates-list {
  margin-bottom: 1rem;
}

.candidate-option {
  margin-bottom: 0.5rem;
}

.success-message {
  background-color: #d4edda;
  color: #155724;
  padding: 1rem;
  border-radius: 4px;
  margin-bottom: 1rem;
}

/* Results page styles */
.results-container {
  max-width: 600px;
  margin: 0 auto;
}

.result-bar {
  height: 30px;
  margin-bottom: 10px;
  background-color: #e9ecef;
  border-radius: 4px;
  overflow: hidden;
}

.result-fill {
  height: 100%;
  background-color: #007bff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  transition: width 1s ease-in-out;
}
